import React, { Component } from 'react'
import './css/FriendsFilter.css'

export default class FriendsFilter extends Component {

    state = {
        filterBy: {
            txt: ''
        }
    }
    onFilterBy = (ev) => {
        const value = ev.target.value;
        this.setState({ filterBy: { txt: value } }, () => this.props.filterFriends(this.state.filterBy))

    }
    onBlur = (ev) => {
        this.setState({ filterBy: { txt: ev.target.value } })
        document.querySelector('.friends-dropdown-div').classList.add('hidden')
    }
    render() {
        return <input className="search-input" autoComplete="off" type="text" name="txt" placeholder="find..."
            onChange={() => this.onFilterBy} onBlur={(ev) => this.onBlur(ev)} onKeyDown={this.onFilterBy}></input>
    }
}