import React, { Component } from 'react';
import { connect } from 'react-redux';
import '../assets/css/Login/login.css'
import {
  loadUsers,
  removeUser,
  login,
  logout,
  signup
} from '../actions/UserActions';
import { Link } from 'react-router-dom';
import loadingGif from './../assets/img/loading.gif'
import UploadService from "../services/UploadService";

class Login extends Component {
  state = {
    msg: '',
    imgUrl: 'https://res.cloudinary.com/ilya432/image/upload/v1580134656/randomuser_itx8lj.png',
    loginCred: {
      email: '',
      password: ''
    },
    signupCred: {
      username: '',
      email: '',
      password: '',
      imgUrl: 'https://res.cloudinary.com/ilya432/image/upload/v1580134656/randomuser_itx8lj.png'
    }
  };

  loginHandleChange = ev => {
    const { name, value } = ev.target;
    this.setState(prevState => ({
      loginCred: {
        ...prevState.loginCred,
        [name]: value
      }
    }));
  };

  signupHandleChange = ev => {
    const { name, value } = ev.target;
    this.setState(prevState => ({
      signupCred: {
        ...prevState.signupCred,
        [name]: value
      }
    }));
  };

  doLogin = async ev => {
    ev.preventDefault();
    const { email, password } = this.state.loginCred;
    if (!email || !password) {
      return this.setState({ msg: 'Please enter user/password' });
    }
    const userCreds = { email, password };
    this.props.login(userCreds);
    this.setState({ loginCred: { email: '', password: '' } });
  };

  doSignup = async ev => {
    ev.preventDefault();
    const { email, password, username, imgUrl } = this.state.signupCred;
    if (!email || !password || !username || !imgUrl) {
      return this.setState({ msg: 'All inputs are required!' });
    }
    // if (imgUrl === '') this.setState({ signupCred: { imgUrl: 'https://res.cloudinary.com/ilya432/image/upload/v1580134656/randomuser_itx8lj.png' } })
    const signupCreds = { email, password, username, imgUrl };
    this.props.signup(signupCreds);
    this.setState({ signupCred: { email: '', password: '', username: '', imgUrl: '' } });
  };

  removeUser = userId => {
    this.props.removeUser(userId);
  };
  onImageSelected = (event) => {
    if (event.target.files[0]) {
      var file = event.target.files[0]
      //show loading
      const loadingImg = document.querySelector('.user-loading-gif')
      loadingImg.style['display'] = 'initial';

      UploadService.uploadImage(file)
        .then(res => {
          console.log('then after loading')
          console.log('UploadService > imgUrl: ' + res.url)
          this.setState({ imgUrl: res.url });
          this.setState({ signupCred: { imgUrl: res.url } });
          // this.setState(prevState => ({
          //   ...prevState,
          //   someProperty: {
          //     ...prevState.someProperty,
          //     someOtherProperty: {
          //       ...prevState.someProperty.someOtherProperty,
          //       anotherProperty: {
          //         ...prevState.someProperty.someOtherProperty.anotherProperty,
          //         flag: false
          //       }
          //     }
          //   }
          // }))

          //hide loading
          const loading = document.querySelector('.user-loading-gif')
          loading.classList.add('hidden');
          loading.style['display'] = 'none'

          //Show uploaded image
          const img = document.querySelector('.user-img')
          img.src = this.state.imgUrl
          img.style['display'] = 'initial'
        })
    }
    else {
      console.log('No image was loaded')
    }

  }
  onUpload = () => {
    const uploadButton = document.querySelector('.upload-hidden-input')
    uploadButton.click()//will open browse
  }
  render() {
    const { imgUrl } = this.state

    let signupSection = (
      <form className="signup-user-container" onSubmit={this.doSignup}>
        <input className="upload-hidden-input" accept="image/*;capture=camera" type="file" onChange={this.onImageSelected}></input>

        <div className="signup-loading-container">
          <img className="user-img" onClick={this.onUpload} title="add you pic.." src={imgUrl} alt="add user pic"></img>
          <img className="user-loading-gif" alt="loading gif" src={loadingGif}></img>
        </div>

        <input className="form-txt-input" type="text" name="username"
          value={this.state.signupCred.username} onChange={this.signupHandleChange} placeholder="Username" />
        <br />
        <input className="form-txt-input" type="text" name="email"
          value={this.state.signupCred.email} onChange={this.signupHandleChange} placeholder="Email" />
        <br />
        <input className="form-txt-input" type="password" name="password"
          value={this.state.signupCred.password} onChange={this.signupHandleChange} placeholder="Password" />
        <br />
        <button className="signup-btn">Signup</button>
      </form>
    );

    let loginSection = (
      <form className="loggedin-user-container" onSubmit={this.doLogin}>
        <input type="text" name="email" value={this.state.loginCred.email}
          onChange={this.loginHandleChange} placeholder="Email"
        />
        <br />
        <input type="password" name="password" value={this.state.loginCred.password}
          onChange={this.loginHandleChange} placeholder="Password"
        />
        <br />
        <button>Login</button>
      </form>
    );

    const { loggedInUser } = this.props;
    return (
      <div className="test">
        <h1 className="login-title">
          Wellcome
        </h1>
        <h2>{this.state.msg}</h2>
        {loggedInUser && (
          <div className="loggedin-user-container">
            <div className="loggedin-user-info flex row">
              <h2 className="loggedin-user-username">{loggedInUser.username} </h2>
              <Link className="" to={{
                pathname: `/user/logged/${loggedInUser._id}`,
                state: { loggedInUser }
              }}  >
                <img className="loggedInUser-img-btn" alt="your pic!" title={loggedInUser.username} src={loggedInUser.imgUrl}></img>
              </Link>
            </div>
            <img className="loguot-btn" alt="logout poc" title="logout" onClick={this.props.logout} src="https://res.cloudinary.com/ilya432/image/upload/v1580038206/logout_geluak.png"></img>
          </div>
        )}

        <div className="login-signup-container flex row">
          {!loggedInUser && loginSection}
          {!loggedInUser && signupSection}
        </div>

        {/* <h2>Login</h2>
        <form>div</form>

        <h2>Signup</h2>
        <form></form> */}

        {/* <button onClick={this.props.loadUsers}>Get All Users</button>
        {this.props.isLoading && 'Loading...'}
        {this.props.users && <ul>

          {this.props.users.map(user => (
            <li key={user._id}>
              <pre>{JSON.stringify(user, null, 2)}</pre>
              <button
                onClick={() => {
                  this.removeUser(user._id);
                }}
              >Remove {user.username}
              </button>
            </li>
          ))}
        </ul>} */}

      </div >
    );
  }
}

const mapStateToProps = state => {
  return {
    users: state.user.users,
    loggedInUser: state.user.loggedInUser,
    isLoading: state.system.isLoading
  };
};
const mapDispatchToProps = {
  login, logout, signup, removeUser, loadUsers
};
export default connect(mapStateToProps, mapDispatchToProps)(Login);
