function getFriends(users, filterBy) {
    return new Promise((resolve) => {
        var friendsToReturn = users
        if (filterBy && filterBy.txt) {
            friendsToReturn = filter(filterBy.txt, friendsToReturn)
        }
        resolve(friendsToReturn)
    })
}

function getFriendById(friends, id) {
    return new Promise((resolve, reject) => {
        const friend = friends.find(friend => friend._id === id)
        friend ? resolve(friend) : reject(`Friend id ${id} not found!`)
    })
}

