function filter(term, friendsToReturn) {
    term = term.toLocaleLowerCase()
    return friendsToReturn.filter(friend => {
        return friend.username.toLocaleLowerCase().includes(term)
        // || return friend.email.toLocaleLowerCase().includes(term)
    })
}



