import React from 'react';
// import { Link } from 'react-router-dom';
import '../assets/css/Feed/feed.css'

// import Like from '../cmps/Like'
import Post from '../cmps/Post'
import Modal from '../cmps/Modal'
import '../assets/css/modals/optionsModal/optionsModal.css'



export default class Feed extends React.Component {

    state = {
        isOptsModalOpen: false,
        postId: ''
    }

    componentDidMount() {
    }

    // post header:
    postOptionsClicked = (postId) => {

        var bg = document.querySelector(`.modal-${postId}, modal-bg`)
        var modal = document.querySelector(`.post-options-modal-${postId}, post-options-modal`)

        if (!this.state.isOptsModalOpen) {
            bg.classList.remove('hidden')

            modal.classList.remove('hidden')
            modal.classList.add('scale-in-hor-center')
            modal.classList.remove('scale-out-horizontal')
            this.setState({ isOptsModalOpen: true })
        } else {
            // bg.style.display = 'hidden'
            modal.classList.add('scale-out-horizontal')
            modal.classList.remove('scale-in-hor-center')
            setTimeout(() => {
                modal.classList.add('hidden')
                bg.classList.add('hidden')
            }, 300);
            this.setState({ isOptsModalOpen: false })
        }
    }


    render() {
        const { users, loggedInUser, posts, reviews, handleChange, likePost, unlikePost, addReview, removePost } = this.props;
        return <div className="feed-container">
            {posts && <ul className="posts-list flex col-rev">
                {posts.map((post, i) => (
                    <li key={post._id}>
                        <Post users={users} loggedInUser={loggedInUser} posts={posts} reviews={reviews} handleChange={handleChange} post={post} addReview={addReview} removePost={removePost}
                            likePost={likePost} unlikePost={unlikePost} postOptionsClicked={this.postOptionsClicked}>
                        </Post>
                        <Modal postId={post._id}>
                            <div className={`post-options-modal-${post._id} post-options-modal flex col`}>
                                <img className={`close-modal-img-btn`} onClick={() => this.postOptionsClicked(post._id)} src="https://res.cloudinary.com/ilya432/image/upload/v1579832736/close_ztguvz.png" alt="close modal" title="close"></img>
                                <span postid={post._id} className={`post-opt`} onClick={() => removePost(post._id)}>delete</span>
                                <span postid={post._id} className={`post-opt`}>share</span>
                                <span postid={post._id} className={`post-opt`}>comment</span>
                            </div>
                        </Modal>
                    </li>
                ))}
            </ul>
            }
        </div>
    }
}