import React from 'react';
import { Router, Switch, Route } from 'react-router';
import history from './history';
// import { BrowserRouter } from 'react-router-dom';
import '../src/assets/css/App/App.css';

// Pages
import Home from './pages/Home.js';
import Login from './pages/Login.js';
import About from './pages/About.js';
// import PostAdd from './cmps/PostAdd.jsx';
import postPage from './pages/postPage.js';
import LoggedInUserPage from './pages/LoggedInUserPage.js';
import UserPage from './pages/UserPage.js';

// Components
import Nav from './cmps/Nav'
// import Footer from './cmps/Footer'

function App() {
  return (
    <div className="App">
      <Router history={history}>
        {/* <BrowserRouter> */}
        <Nav />
        <Switch>
          <Route path="/" component={Home} exact />
          <Route path="/about/:id" component={About} exact />
          <Route path="/post/:id" component={postPage} exact />
          <Route path="/user/:id" component={UserPage} exact />
          <Route path="/user/logged/:id" component={LoggedInUserPage} exact />
          {/* <Route path="/post:id" component={PostAdd} exact /> */}
          {/* <Route path="/post" component={PostsList} exact /> */}
          <Route path="/login" component={Login} exact />
          {/* <Route path="/" component={About} exact /> */}
        </Switch>
        {/* </BrowserRouter> */}
      </Router>
    </div>
  );
}
export default App;
