import React from 'react';
import { Router } from 'react-router'
import { Link } from 'react-router-dom';
import '../assets/css/Post/post.css'
import Like from '../cmps/Like'
// import UserPage from '../pages/UserPage';

export default class Post extends React.Component {
    constructor(context) {
        super(context)
    }
    state = {
        postToEdit: { _id: '', txt: '', aboutUserId: '', byUserId: '', username: '' },
        // posts: [], 
        reviews: [],
        reviewToEdit: { _id: '', txt: '', aboutUserId: '', byUserId: '', username: '' },
        whoLiked: '',
    };

    componentDidMount() {
    }

    countComments = (post) => {
        let commentsCount = post.commentedBy.length
        let result = ''
        if (commentsCount > 1) {
            result = 'show all ' + commentsCount + ' comments...';
        } else if (commentsCount === 0) {
            result = 'no comments'
        } else {
            result = 'show 1 comment...';
        }
        return result;
    }
    handleChange = (ev, postId) => {
        const { name, value } = ev.target;
        if (name === 'txt') {
            const addCommentBtn = document.querySelector(`.btn-${postId}`)
            if (value === '') {
                addCommentBtn.classList.remove('highligt');
            } else {
                addCommentBtn.classList.add('highligt');
            }
        }
        this.setState(prevState => ({
            reviewToEdit: {
                ...prevState.reviewToEdit,
                [name]: value
            }
        }));
    };
    textAreaChange = ev => {
        const { name, value } = ev.target;
        this.setState(prevState => ({
            postToEdit: {
                ...prevState.postToEdit,
                [name]: value
            }
        }));
    };
    addReview = ev => {
        ev.preventDefault();
        this.props.addReview(this.state.reviewToEdit);
        this.setState({ reviewToEdit: { txt: '', aboutUserId: '' } });
    };
    countLikes = (post) => {
        let likesCount = post.likedBy.length
        let result = ''
        if (likesCount > 1) {
            result = likesCount + ' likes';
        } else if (likesCount === 0) {
            result = ''
        } else {
            result = '1 like';
        }
        return result;
    }
    seeWhoLiked = (post, e) => {
        let elSpan = e.target
        let whoLiked = ''
        post.likedBy.forEach((user, i) => {
            if (i !== post.likedBy.length - 1 && i < 10) {
                whoLiked += user.nickName + ', \n'
            }
            else {
                whoLiked += user.nickName + ' '
                return;
            }
        });
        elSpan.lable = whoLiked;
        this.setState({ whoLiked: whoLiked })
    }
    // toUserProfile = (user) => {
    //     // alert(user.nickName)
    //     // history.push(`/user/${user._id}`);
    //     // this.context.router.push({ pathname: `/user/${user._id}`, state: { user } })
    // }

    render() {
        const { users, reviews, post, loggedInUser, likePost, postOptionsClicked, handleChange, onLikeClicked } = this.props;
        const { user } = post.owner
        return <article className="post-container">

            <div className="post-header">
                <div className="post-header-img-container flex row">
                    <div>
                        <Link className="" to={{
                            pathname: `/user/${post.owner._id}`,
                            state: { user }
                        }}  >
                            <img className="post-header-user-img" alt="to user page" src={post.owner.imgUrl}></img>
                        </Link>
                        <span className="post-header-user-nickname">{post.owner.nickName}</span>
                    </div>
                    <img className="post-header-options-btn" onClick={() => postOptionsClicked(post._id)} src="https://res.cloudinary.com/ilya432/image/upload/v1579824524/options_unhkvi.png" alt="options pic" title="options"></img>
                </div>
            </div>

            <div className="post-img-container">
                <img className="post-img" alt="pic url" src={post.imgUrl}></img>
            </div>

            <div className="post-body-container">
                <div className="post-buttons-container flex row">
                    {loggedInUser &&
                        <Like post={post} loggedInUser={loggedInUser} onLikeClicked={onLikeClicked} likePost={likePost} />}
                    <Link to={`/about/${post._id}`}><img className="post-btn" alt="see post" title="see post" src={"https://res.cloudinary.com/ilya432/image/upload/v1579905174/comment_l3rb1o.png"}></img></Link>
                    <img className="post-btn" alt="share" title="share" src={"https://res.cloudinary.com/ilya432/image/upload/v1579905163/share_owvhnz.png"}></img>
                </div>

                <div className="post-body-data-section flex row">
                    {post.likedBy &&
                        <span className="post-show-likes" title={this.state.whoLiked} onMouseOver={(e) => this.seeWhoLiked(post, e)}>{this.countLikes(post)}</span>}
                </div>

                <div className="post-body-data flex col">

                    <div className="post-body-data-section flex row">
                        <span className="post-body-user-nickname">{post.owner.nickName}</span>
                        <span className="post-body-user-txt">{post.txt}</span>
                        <span className="post-body-user-tags-span">{post.tags && <ul className="post-tags-list flex row">
                            {post.tags.map((tag, i) => (
                                <li key={i}>
                                    <span className="post-body-user-tag">{'#' + tag}</span>
                                </li>
                            ))}
                        </ul>}</span>
                    </div>


                    <div className="post-body-data-section flex row">
                        {post.commentedBy &&
                            <Link to={`/about/${post._id}`}><span className="post-show-all-comments"> {this.countComments(post)}</span></Link>}
                    </div>

                    <div className="flex row">
                        {this.props.reviews && <ul>
                            {reviews.map(review => (
                                <li key={review._id}>
                                    <h3>{review.txt}</h3>
                                    <p>
                                        <Link to={`user/${review.aboutUser._id}`}> About {review.aboutUser.username}</Link>
                                    </p>
                                    <p>
                                        <Link to={`user/${review.byUser._id}`}> By {review.byUser.username} </Link>
                                    </p>
                                </li>
                            ))}
                        </ul>}

                        {users && loggedInUser &&
                            <select className="comment-select-user" onChange={this.handleChange} value={this.state.reviewToEdit.aboutUserId} name="aboutUserId">
                                <option value="">Select User</option>
                                {users.map(user => (<option key={user._id} value={user._id}> {user.username} </option>
                                ))}
                            </select>
                        }

                        <form className="comment-container flex row" onSubmit={this.addReview}>
                            <div className="add-comment-container">
                                <input className="add-comment-textarea" name="txt" type="text" onChange={(ev) => this.handleChange(ev, post._id)} value={this.state.reviewToEdit.txt}></input>
                                <div className="add-comment-container-absolute">
                                    <span className={`add-comment-button btn-${post._id}`} onClick={this.addReview}>Post</span>
                                </div>
                            </div>
                        </form>


                    </div>
                </div>
            </div>
        </article >
    }

}

