import React from 'react';
import FriendsFilter from '../cmps/FriendsFilter/FriendsFilter'
import '../assets/css/search/search.css'
import FriendPreview from '../cmps/FriendPreview'

// React/Redux
import { connect } from 'react-redux';
import { loadUsers } from '../actions/UserActions.js';
import { loadFriends, loadSelectedFriend } from '../actions/FriendActions.js';

class Search extends React.Component {
    state = {
        selectedFriend: ''
    }
    componentDidMount() {
        this.props.loadUsers()
    }
    filterFriends = (filterBy) => {
        (filterBy) ? this.props.loadFriends(this.props.users, filterBy)
            : this.props.loadFriends();
        // : document.querySelector('.friends-dropdown-div').classList.add('hidden')

        this.handleDropDown(filterBy.txt)
    }
    handleDropDown = (txt) => {
        ((this.props.friends) ? document.querySelector('.friends-dropdown-div').classList.remove('hidden')
            : document.querySelector('.friends-dropdown-div').classList.add('hidden'))
    }
    onSelectFriend = (friend) => {
        console.log(friend.username)
        this.props.loadSelectedFriend(this.props.friends, friend._id)
            .then(() => {
                this.props.history.push({
                    pathname: `/user/${this.props.selectedFriend._id}`,
                    state: {
                        selectedFriend: this.props.selectedFriend
                    }
                });
            })
    }

    render() {
        const { friends } = this.props
        return <div className="search-container">
            <FriendsFilter className="search-input" filterFriends={this.filterFriends}></FriendsFilter>
            <div>
            </div>
            <div className="friends-dropdown-div hidden">
                {friends && <ul className="friends-list flex col">{friends.map((friend) =>
                    <FriendPreview key={friend._id} friend={friend} onSelectFriend={() => this.onSelectFriend(friend._id)} />
                )}
                </ul>}
            </div>
        </div>
    }
}

const mapStateToProps = state => {
    return {
        users: state.user.users,
        friends: state.friend.friends,
        selectedFriend: state.friend.selectedFriend,
        loggedInUser: state.user.loggedInUser
    };
};
const mapDispatchToProps = {
    loadUsers,
    loadFriends,
    loadSelectedFriend
};

export default connect(mapStateToProps, mapDispatchToProps)(Search);