import FriendService from '../services/FriendService';

export function loadFriends(users, filterBy) {
    return async dispatch => {
        try {
            const friends = await FriendService.getFriends(users, filterBy);
            dispatch(setFriends(friends));

        } catch (err) {
            console.log('FriendActions: err in loadFriends', err);
        }
    };
}

export function loadSelectedFriend(friends, friendId) {
    return async dispatch => {
        try {
            const friend = await FriendService.getFriendById(friends, friendId);
            dispatch(setSelectedFriend(friend));
        } catch (err) {
            console.log('FriendActions: err in loadFriend', err);
        }
    };
}
export function addFriend(friend) {
    return async dispatch => {
        try {
            const addedFriend = await FriendService.add(friend);
            dispatch(_addFriend(addedFriend));
        } catch (err) {
            console.log('FriendActions: err in addFriend', err);
        }
    };
}

function setFriends(friends) {
    return {
        type: 'SET_FRIENDS',
        friends
    };
}
function setSelectedFriend(friend) {
    return {
        type: 'SET_SELECTED_FRIEND',
        friend
    };
}
function _addFriend(friend) {
    return {
        type: 'FRIEND_ADD',
        friend
    };
}
