import React, { Component } from 'react';
import '../assets/css/FriendPreview/FriendPreview.css'

export default class ContactPreview extends Component {
    onSelectFriend = (contactId) => {
        alert(contactId)
        if (this.props.onSelectFriend)
            this.props.onSelectFriend(contactId);
    }
    render() {
        const { friend } = this.props;
        return <li className="friend-item flex row"  >
            <img className="friend-img flex row" src={friend.imgUrl} alt="friend's pic" onClick={() => this.onSelectFriend(friend._Id)} />
            <span className="friend-nickname flex row">{friend.username}</span>
        </li>
    }
}