export default {
    getPosts,
    likePost,
    unlikePost,
    add,
    remove
}
// function getPosts() {
//     return postsJson;
// }
async function getPosts() {
    try {
        const posts = await HttpService.get('post');
        return posts;
    }
    catch (err) {
        console.log(err)
    }
}
async function add(post) {
    try {
        const addedPost = await HttpService.post(`post`, post);
        return addedPost

    } catch (err) {
        console.log(err)
    }
}
function remove(postId) {
    return HttpService.delete(`post/${postId}`);
}

async function likePost(likedByObj, post) {
    const postId = post._id;
    console.log('frontend -> postService -> likePost -> postId: ', postId)
    console.log('frontend ... -> likeByObj: ', likedByObj)
    try {
        const res = await HttpService.post(`post/like`,
            { likedByObj, postId }
        );
        return res;
    } catch (err) {
        console.log('likePost failed Error: ', err)
    }

}
async function unlikePost(unlikedByObj, post) {
    const postId = post._id;
    console.log('frontend -> postService -> unlikePost -> postId: ', postId)
    console.log('frontend ... -> unlikeByObj: ', unlikedByObj)
    try {
        const res = await HttpService.put(`post/unlike`,
            { unlikedByObj, postId }
        );
        return res;

    } catch (err) {
        console.log('likePost failed Error: ', err)
    }
}