var CryptoJS = require('crypto-js');


const CLOUD_NAME = 'ilya432'
const PRESET_NAME = 'Ilya432_preset'
const API_KEY = '796597933171765'
const UPLOAD_URL = `https://api.cloudinary.com/v1_1/${CLOUD_NAME}/image/upload`
const API_SECRET = 'UOrgge2sLbm_EveKUC5VfZl7z3M'

function uploadImage(file) {
    console.log(file)
    const data = new FormData();
    data.append('file', file)
    data.append('upload_preset', PRESET_NAME);
    return fetch(UPLOAD_URL, {
        method: 'POST',
        body: data
    })
        .then(res => res.json())
        .then(res => {
            return res
        })
        .catch(err => console.error(err))
}
function uploadImageMobile(uri, callback) {
    let timestamp = (Date.now() / 1000 | 0).toString();
    let hash_string = 'timestamp=' + timestamp + API_SECRET
    let signature = CryptoJS.SHA1(hash_string).toString();
    let upload_url = UPLOAD_URL

    let xhr = new XMLHttpRequest();
    xhr.open('POST', upload_url);
    xhr.onload = () => {
        console.log(xhr);
        callback(JSON.parse(xhr._response)) // this line is the response call back to your request!!!!
    };
    let data = new FormData();
    data.append('file', { uri: uri, type: 'image/png', name: 'upload.png' });
    data.append('timestamp', timestamp);
    data.append('api_key', API_KEY);
    data.append('signature', signature);
    xhr.send(data);
}
export default {
    uploadImage,
    uploadImageMobile
}