import React, { Component } from 'react';
import { connect } from 'react-redux';
import '../assets/css/LoggedinUserPage/loggedinUserPage.css'
import { loadPosts } from '../actions/PostActions.js';
import { loadUsers } from '../actions/UserActions.js';

class LoggedInUserPage extends Component {
    state = {
        postsCount: ''
    }
    componentDidMount() {
        this.props.loadUsers()
        this.props.loadPosts()
        this.countComments(this.props.posts)
    }

    countComments = (posts) => {
        let postsCount = 0
        posts.forEach(post => {
            if (post.owner._id === this.props.loggedInUser._id) {
                postsCount++
            }
        });

        let result = ''
        if (postsCount > 1) {
            result = postsCount + ' posts';
        } else if (postsCount === 0) {
            result = 'no posts'
        } else {
            result = '1 post';
        }
        this.setState({ postsCount: result })
    }

    render() {
        const { loggedInUser } = this.props
        return (<div className="user-page-container">

            <div className="user-info flex col">
                <div className="image-username-container flex row">
                    <img className="user-img" src={loggedInUser.imgUrl}></img>
                    <div className="user-socual-info-container flex col">
                        <span className="username">{loggedInUser.username}</span>
                        <div className="socual-info-section flex row">
                            <span className="user-info-field username">{this.state.postsCount}</span>
                            <span className="user-info-field username">folowers: </span>
                            <span className="user-info-field username">folowing: </span>
                        </div>
                    </div>
                </div>

            </div>

        </div>
        )
    }
}

const mapStateToProps = state => {
    return {
        loggedInUser: state.user.loggedInUser,
        posts: state.post.posts
    };
};
const mapDispatchToProps = {
    loadUsers, loadPosts
};

export default connect(mapStateToProps, mapDispatchToProps)(LoggedInUserPage);