import React from 'react';
// import PostForm from "../cmps/PostForm";
import loadingGif from './../assets/img/loading.gif'

import '../assets/css/PostAdd/postAdd.css'
import UploadService from "../services/UploadService";

export default class PostAdd extends React.Component {
    state = {
        selectedFile: null,
        postImgUrl: null,
        postTxt: '',
        postTagsText: '',
        tags: []
    }

    onUpload = () => {
        const uploadButton = document.querySelector('.upload-hidden-input')
        uploadButton.click()//will open browse
    }
    onImageSelected = (event) => {
        if (event.target.files[0]) {
            this.setState({ selectedFile: event.target.files[0] })

            var file = event.target.files[0]

            //show loading
            const loadingImg = document.querySelector('.loading-gif')
            loadingImg.style['display'] = 'initial';

            //show form
            const formn = document.querySelector('.post-add-form')
            formn.style['display'] = 'initial'

            UploadService.uploadImage(file)
                .then(res => {
                    console.log('then after loading')
                    console.log('UploadService > imgUrl: ' + res.url)
                    this.setState({ postImgUrl: res.url })

                    //hide loading
                    const loading = document.querySelector('.loading-gif')
                    loading.classList.add('hidden');
                    loading.style['display'] = 'none'

                    //Show uploaded image
                    const img = document.querySelector('.post-preview-img')
                    img.src = this.state.postImgUrl
                    img.style['display'] = 'initial'
                })
        }
        else {
            console.log('No image was loaded')
        }

    }
    onAddPost = (e) => {
        e.preventDefault()

        var date = new Date();
        var timestamp = date.getTime();

        var tagsTxt = this.state.postTagsText
        const tags = tagsTxt.toLowerCase().split(",").filter(function (tag) {
            return tag !== '';
        });

        var postObj = {
            _id: '4',
            imgUrl: this.state.postImgUrl,
            createdAt: timestamp,
            owner: {
                _id: this.props.loggedInUser._id,
                nickName: this.props.loggedInUser.username,
                imgUrl: this.props.loggedInUser.imgUrl
            },
            txt: this.state.postTxt,
            tags: tags,
            likedBy: [],
            commentedBy: []
        }
        this.finallySubmit(postObj);
    }
    finallySubmit = postObj => {
        this.props.addPost(postObj)
        const box = document.querySelector('.post-add-form')
        box.style['display'] = 'none'
    };

    changePostTxtInput = (ev) => {
        this.setState({ postTxt: ev.target.value })
    }
    changePostTagsInput = (ev) => {

        this.setState({ postTagsText: ev.target.value.trim() })
    }

    render() {
        // var { loggedInUser, posts, textAreaChange } = this.props
        return <div className="PostAdd">

            <input className="upload-hidden-input" type="file" onChange={this.onImageSelected}></input>

            <form className="post-add-form" onSubmit={this.onAddPost}>
                <button className="upload-img-button" onClick={this.onUploadImage} >Upload image</button>
                {/* <textarea className="new-post-txt-area" name="txt" onChange={this.textAreaChange} value={this.state.postToEdit.txt}></textarea> */}

                <div className="loading-container">
                    <img className="post-preview-img" src={this.state.postImgUrl} alt="post preview pic" title="pic preview"></img>
                    <img className="loading-gif" alt="load gif" src={loadingGif}></img>
                </div>
                <div>
                    <label>Say something..</label>
                    <input className="add-post-txt" type="text" onChange={this.changePostTxtInput} value={this.state.postTxt}></input>

                    <label>Tags(,)</label>
                    <input className="add-post-tags" type="text" onChange={this.changePostTagsInput} value={this.state.postTagsText}></input>
                    <button className="post-preview-submit-btn">Submit</button>
                </div>
            </form>

            <img className="add-post-img-btn" onClick={this.onUpload} title="add post" src={"https://res.cloudinary.com/ilya432/image/upload/v1579691874/makePost_c9uazs.png"} alt="ass post pic"></img>
        </div>
    }
}








